<template>
  <div id="bloger-info">
    <div class="header">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="manage">
      <div class="title">
        
      </div>
      <div class="manage-pwd">
        <div class="label"></div>
        <el-input
          v-model="loginForm.username"
          placeholder=""
        ></el-input>
        <div class="label"></div>
        <el-input
          placeholder=""
          v-model="loginForm.password"
          show-password
        ></el-input>
        <div class="options" style="margin:10px 0;">
          <el-button type="primary" icon="el-icon-edit" @click="setLoginForm"
            ></el-button
          >
        </div>
      </div>
    </div>
    <div class="manage">
      <div class="title">
        
      </div>
      <div class="manage-pwd">
        <div class="label"></div>
        <el-input placeholder="" v-model="loginForm.name"></el-input>
        <div class="label">Git</div>
        <el-input v-model="loginForm.git" placeholder="Git"></el-input>
        <div class="label"></div>
        <el-input
          placeholder=""
          v-model="loginForm.weibo"
        ></el-input>
        <div class="label"></div>
        <el-upload
          class="avatar-uploader"
          action="http://127.0.0.1:3000/upload/uploadimg"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
        >
          <img v-if="loginForm.avatar" :src="loginForm.avatar" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
        <div class="options" style="margin:10px 0;">
          <el-button type="primary" @click="setBlogerForm"></el-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "blogerInfo",
  data() {
    return {
      id: null,
      loginForm: {
        username: "",
        password: "",
        git: null,
        weibo: null,
        avatar: "",
        name: ""
      }
    };
  },
  methods: {
    // 
    async getLoginForm() {
      const { data: res } = await this.http.get(
        "http://127.0.0.1:3000/admin/loginform"
      );
      console.log(res);
      this.loginForm.username = res.data.username;
      this.loginForm.password = res.data.password;
      this.id = res.data.id;
      this.loginForm.git = res.data.git;
      this.loginForm.weibo = res.data.weibo;
      this.loginForm.avatar = res.data.avatar;
      this.loginForm.name = res.data.name;
    },
    //
    async setLoginForm() {
      const { data: res } = await this.http.put(
        "http://127.0.0.1:3000/admin/updateloginform",
        {
          password: this.loginForm.password,
          username: this.loginForm.username,
          id: this.id
        }
      );
      console.log(res);
      if (res.meta.status === 200) {
        this.$message.success("");
      } else {
        this.$message.error("");
      }
    },
    //
    async setBlogerForm() {
      const { data: res } = await this.http.put(
        "http://127.0.0.1:3000/admin/setblogerinfo",
        {
          git: this.loginForm.git,
          weibo: this.loginForm.weibo,
          avatar: this.loginForm.avatar,
          name: this.loginForm.name,
          id: this.id
        }
      );
      console.log(res);
      if (res.meta.status === 200) {
        this.$message.success("");
      } else {
        this.$message.error("");
      }
    },
    handleAvatarSuccess(res) {
      console.log(res.data.url);
      console.log(this.loginForm.avatar);
      this.loginForm.avatar = res.data.url;
      // this.loginForm.avatar = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error(" JPG !");
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isJPG && isLt2M;
    }
  },
  created() {
    console.log(this.loginForm);
    this.getLoginForm();
  }
};
</script>
<style lang="less">
#bloger-info {
  background: #fff;
  padding: 15px;
  .header {
    margin-bottom: 25px;
  }
  .title {
    font-size: 20px;
    font-weight: bold;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed red !important;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409eff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
}
</style>
